import React from "react";
import { Switch, Route } from "react-router-dom";
import LandingPage from "./LandingPage";

import Contact from "./Contact";
import Resume from "./Resume";
import Skills from "./Skills";

const Main = () => (
  <Switch>
    <Route exact path="/maramsite" component={LandingPage} />
    <Route path="/maramsite/skills" component={Skills} />
    <Route path="/maramsite/contact" component={Contact} />
    <Route path="/maramsite/resume" component={Resume} />
  </Switch>
);

export default Main;



