import React, { Component } from "react";

class LandingPage extends Component {
  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col s12 center-align">
            <img
              src="https://cdn0.iconfinder.com/data/icons/streamline-emoji-1/48/164-woman-technologist-2-512.png"
              alt="avatar"
              className="avatar-img"
            />
            <div className="card-panel hoverable teal accent-4 ">
              <h1>FULL STACK DEVELOPER</h1>
              <hr />
              <p>
                | HTML | CSS | Bootstrap | React JS | SQL Server | MongoDB |
                ASP.net | WEP API | MVC | C#.net |
              </p>
              <div
                style={{
                  display: "flex",
                  width: "70%",
                  margin: "auto",
                  paddingBottom: "10px",
                  justifyContent: "space-between"
                }}
              >
                <a
                  href="https://plus.google.com/u/0/104549865086849400912"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i className="fab fa-google-plus fa-3x icon-color" />
                </a>
                <a
                  href="https://github.com/MaramSb"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i className="fab fa-github fa-3x icon-color" />
                </a>
                <a
                  href="https://www.linkedin.com/in/maram-shbeeb-4aa105a1/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i className="fab fa-linkedin fa-3x icon-color" />
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default LandingPage;



