import React, { Component } from "react";
import { Navbar, NavItem, Button } from "react-materialize";
import Main from "./components/main";
//import { Link } from "react-router-dom";
import "./App.css";

class App extends Component {
  render() {
    return (
      <div>
        <Navbar
          href="/maramsite"
          id="navbar"
          className="blue-grey darken-2"
          brand="MARAM PORTFOLIO"
          right
        >
          <NavItem href="/maramsite/resume">My History</NavItem>
          <NavItem href="/maramsite/skills">Skills</NavItem>
          <NavItem href="/maramsite/contact">Contact</NavItem>
        </Navbar>

        <div className="fixed-action-btn horizontal">
          <Button
            href="/"
            floating
            fab="horizontal"
            icon="mode_edit"
            className="teal accent-4"
            large
            style={{ bottom: "45px", right: "24px" }}
          >
            <a
              className="btn-floating btn-large waves-effect waves-light teal accent-2"
              href="/maramsite/skills"
            >
              <i className="material-icons">details</i>
            </a>
            <a
              className="btn-floating btn-large waves-effect waves-light teal accent-2"
              href="/maramsite"
            >
              <i className="material-icons">info</i>
            </a>
            <a
              className="btn-floating btn-large waves-effect waves-light teal accent-2"
              href="/maramsite/contact"
            >
              <i className="material-icons">message</i>
            </a>
          </Button>
        </div>
        <div>
          <Main />
        </div>
      </div>
    );
  }
}

export default App;



