import React, { Component } from "react";
import "../App.css";
class Contact extends Component {
  render() {
    return (
      <div className="section container">
        <div className="row">
          <div className="col s12 m4 l3 center-align hoverable contact-grid">
            <h2 className="h2-style">Maram Shbeeb</h2>
            <hr className="pink" />
            <img
              src="https://cdn0.iconfinder.com/data/icons/streamline-emoji-1/48/164-woman-technologist-2-512.png"
              alt="avatar"
              style={{ height: "250px" }}
            />
            <p>
              Full Stack Developer .net specialist with rational and
              non-rational database. Design overall architecture of the web
              application. Maintain quality and ensure responsiveness of
              applications. I am also a computer instructor, I provide
              programming courses and ICDL courses.
            </p>
            <br />
            <blockquote style={{ fontSize: "15px" }} className="left-align">
              Before software can be reusable it first has to be usable...
              <span>
                <strong> Ralph Johnson </strong>
              </span>
            </blockquote>
          </div>
          <div className="col s12 m4 l3 hoverable">
            <div className="center-align">
              <h2 className="h2-style">Contact Me</h2>
              <hr className="pink" />
            </div>
            <div className="section contact-list">
              <ul>
                <ol>
                  {" "}
                  <i
                    className="fab fa-instagram fa-2x icon-color"
                    aria-hidden="true"
                  />
                  <span className="a">maramshbeeb</span>
                </ol>
                <ol>
                  {" "}
                  <i
                    className="fab fa-skype fa-2x icon-color"
                    aria-hidden="true"
                  />
                  <span className="a"> maramshbeeb </span>
                </ol>
                <ol>
                  <i
                    className="fa fa-envelope fa-2x icon-color"
                    aria-hidden="true"
                  />

                  <span>
                    <a
                      className="a"
                      target="_blank"
                      rel="noopener noreferrer"
                      href="maramshbeeb@gmail.com"
                    >
                      maramshbeeb@gmail.com
                    </a>
                  </span>
                </ol>
                <ol>
                  <i
                    className="fab fa-linkedin fa-2x icon-color "
                    aria-hidden="true"
                  />
                  <span>
                    {" "}
                    <a
                      className="a"
                      target="_blank"
                      rel="noopener noreferrer"
                      href="https://www.linkedin.com/in/maram-shbeeb-4aa105a1/"
                    >
                      Maram Shbeeb
                    </a>
                  </span>
                </ol>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;



