import React, { Component } from "react";
import Education from "./Education";
import Experience from "./Experiance";

class Resume extends Component {
  render() {
    return (
      <div className="section container">
        <div className="row">
          <div className="col s12 m4 l3 hoverable white edu">
            <div className="section" />
            <div className="center-align">
              <i className="fas fa-graduation-cap fa-7x icon-color" />
              <h2 className="h2-style">Education</h2>
            </div>
            <div className="resume">
              <Education
                startYear={2016}
                endYear={2018}
                company="Self Learning"
                schoolName="Online Courses"
                schoolDescription="Pluraslight: C#, ASP.net Core, MVC, WEB API, MongoDB. 
                Coursera: Web development (HTML, CSS, JAVASCRIPT) "
              />

              <Education
                startYear={2011}
                endYear={2015}
                company="SVU"
                schoolName="Syrian Virtual University"
                schoolDescription="•	Field of Study: Bachelor Information Technology
               •	Graduation Date: June 25, 2015
              •	Grade Point Average: 72.58
              •	Rate: Very Good
              "
              />

              <Education
                startYear={2008}
                endYear={2010}
                company="DTC"
                schoolName="Damascus Training Center"
                schoolDescription="•	Field of Study: Information Technology
              •	Graduation Date: July 22, 2010
              •	Grade Point Average: 84.8
              •	Rate: Very Good
              "
              />
            </div>
          </div>

          <div className="col s12 m4 l3 hoverable white exp">
            <div className="section" />
            <div className="center-align">
              <i className="fas fa-laptop-code fa-7x icon-color" />
              <h2 className="h2-style">Experiances</h2>
            </div>

            <div className="resume">
              <Experience
                jobName="Backend Developer (Short Term)"
                jobDescription="Web development (Asp.net Core – MVC – Web API – React JS), 
                Database design (Mongo DB / SQL Server),
                System Analysis."
                startYear={2018}
                endYear={2018}
                company="ATAI Soft"
              />

              <Experience
                startYear={2017}
                endYear={2018}
                company="SFPA"
                jobName="Computer (ICDL) Instructor"
                jobDescription="Prepare and Provide ICDL Courses, Manage Students Documents and Provide Reports."
              />

              <Experience
                startYear={2010}
                endYear={2013}
                company="Almounkez"
                jobName=".NET Developer"
                jobDescription="Develop Windows Applications Using Visual Studio (vb.net) with Devexpress Library,
                Database Management & Design Using SQL Server, Reporting Using Stimulsoft Reports and System Analysis"
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Resume;



